.header-bg{
    background-image: url(../../images/bradcam.png);
}
.header-area{
    background-size: cover;
    background-position: center center;
    padding: 100px 0;
    background-position: bottom;
    background-repeat: no-repeat;
    height: 200px;
}

.testimonials-area{
    height: 800px;
    margin-top: 20px;
}

.testimonial-services{
    background: #fbf9ff;
    margin-bottom: 15px;
    border-radius: 25px;
}
.testimonial-services input,
.testimonial-services button{
    background: white;
    border: 1px solid var(--orange);
    border-radius: 25px;
}
.testimonial-services input::placeholder{
    text-align: center;
}
.testimonial-img-area{
    height: 350px;
    width: 300px;
}
.testimonial-detail{
    height: 350px;
}
.testimonial-detail .heading{
    color: var(--orange);
}
.testimonial-detail .badge{
    background: rgb(200, 200, 200);
    font-size: 10px;
}
.testimonial-detail .fa-solid{
    color: var(--orange);
    font-size: 25px;

}
.testimonial-img-area img{
    height: 100%;
    width: 100%;
    background-size: cover;
    /* border-radius: 25px; */
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
}

.signle-testimonial{
    border: 1px solid gray;
    border-radius: 25px;
}